@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

body {
  margin: 0px;
  font-family: "Roboto", sans-serif;
}

.nav-bar {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  color: rgb(255, 255, 255);
  display: flex;
  justify-content: space-between;
}

.nav-bar > h1 {
  margin: 0px;
  padding: 30px;
}

.nav-bar > #dateDiv {
  padding: 22px;
  text-transform: uppercase;
}

.nav-bar > #dateDiv > div {
  margin: 8px;
}

.body_padding_div {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  height: 100vh;
}

.form {
  margin: 25px;
  display: flex;
  justify-content: center;
}

#city {
  padding: 15px;
  border-radius: 50px;
  font-size: 18px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  border: none;
  text-align: center;
}

::-webkit-input-placeholder {
  /* Edge */
  color: red;
}

::placeholder {
  color: rgb(255, 255, 255);
  /* text-align: center; */
}

.city_name {
  color: rgb(255, 255, 255);
  text-shadow: 3px 3px 4px #000000;
}

#tempC {
  font-size: 150px;
  font-weight: bold;
  color: rgb(255, 255, 255);
  margin-left: 10px;
  text-shadow: 2px 2px 8px #000000;
}

.parentDiv2 {
  color: rgb(255, 255, 255);
  text-shadow: 3px 3px 4px #000000;
}

#main_container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin: 18px;
  padding: 18px;
}

#weather_icon {
  width: 172px;
}

#forecastDiv {
  display: flex;
  align-items: center;
  justify-content: center;
  /* border: 2px solid #fffc3a; */
  border-radius: 12px;
  width: 100%;
}


#left,
#right {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#details {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 15px;
}

.flex {
  display: flex;
  justify-content: space-around;
}

/* footer */
.iconscontainer {
  list-style: none;
  display: flex;
  fill: #ffffff;
  margin: 15px;
}

.icon {
  padding-right: 13px;
  fill: white;
  width: 18px;
  cursor: pointer;
}

.icon:hover {
  fill: #fffc3a;
}

footer {
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 25px;
  background: rgba(14, 154, 224, 0.4);
  backdrop-filter: blur(5px);
  padding: 18px 0px;
}

footer > p {
  color: white;
  font-size: 20px;
}

